a{
  color: inherit;
  font-size: 16px;
}
ul,p{
  margin: 0;
}
.Rules{
  display: none;
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.7);
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-size: 10px;
  word-wrap: break-word;
  overflow: scroll;
}
.rules-list{
  max-width: 80%;
  max-height: 90%;
  list-style: none;
}
.rules-modal-close{
  position: fixed;
  display: inline-block;
  top: 10px;
  right: 10px;
  width: 10px;
  height: 10px;
  cursor: pointer;
  border: 5px solid #f56b00;
  background:
  linear-gradient(45deg, rgba(0,0,0,0) 0%,rgba(0,0,0,0) 43%,#fff 45%,#fff 55%,rgba(0,0,0,0) 57%,rgba(0,0,0,0) 100%),
  linear-gradient(135deg, #f56b00 0%,#f56b00 43%,#fff 45%,#fff 55%,#f56b00 57%,#f56b00 100%);
}

.Shieldscreen{
  display: none;
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 100;
  background-color: rgba(0, 0, 0, 0.7);
  justify-content: center;
  align-items: center;
  flex-direction: column;
  overflow: scroll;
}
.modal{
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
  background-color: #0e1621;
  border-radius: 10px;
  padding: 10px 10px;
  min-width: 50%;
  max-width: 70%;
  max-height: 95%;
}
.final-screen{
  background-color: #f56b00;
}
.modal-header>h1{
  margin-top: 10px;
}
.modal-close{
  position: absolute;
  display: inline-block;
  top: 10px;
  right: 10px;
  width: 10px;
  height: 10px;
  cursor: pointer;
  border: 5px solid #f56b00;
  background:
  linear-gradient(45deg, rgba(0,0,0,0) 0%,rgba(0,0,0,0) 43%,#fff 45%,#fff 55%,rgba(0,0,0,0) 57%,rgba(0,0,0,0) 100%),
  linear-gradient(135deg, #f56b00 0%,#f56b00 43%,#fff 45%,#fff 55%,#f56b00 57%,#f56b00 100%);
}
.final-close{
  width: 10px;
  height: 10px;
  border: 4px solid #2b5278;
  background:
  linear-gradient(45deg, rgba(0,0,0,0) 0%,rgba(0,0,0,0) 43%,#fff 45%,#fff 55%,rgba(0,0,0,0) 57%,rgba(0,0,0,0) 100%),
  linear-gradient(135deg, #2b5278 0%,#2b5278 43%,#fff 45%,#fff 55%,#2b5278 57%,#2b5278 100%);
}
.modal-body{
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  max-width: 80%;
}
.modal-inputs{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}
.modal-inputs>div{
  position: relative;
}
.icon-modal-input{
  width: 20px;
  height: 20px;
  position: absolute;
  right: 5%;
  top: 30%;
}
.icon-modal-output{
  width: 20px;
  height: 20px;
  position: absolute;
  right: 5%;
  top: 30%;
}
.modal-input{
  outline: none;
  box-sizing: border-box;
  font-size: 20px;
  line-height: 27px;
  border-radius: 8px;
  padding: 10.5px 26px;
  margin: 2px 0px;
  border: 1px solid transparent;
  color: #2F3E4C;
  height: 50px;
  background: #EFF5FB;
  width: 95%;
}
.arrowDown{
  border: solid #f56b00;
  height: 3px;
  width: 3px;
  border-width: 0 5px 5px 0;
  display: inline-block;
  padding: 3px;
    transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
}
.checkout-info{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  text-align: left;
  width: 100%;
}
.checkout-info>p{
  font-size: 14px;
  max-width: 80%;
  color: #6484a3;
}
.checkout-info>h2{
  text-shadow: 1px  1px 4px #2b5278;
  line-height: 40px;
}

.info-list-wrap{
  margin: 10px auto;
  width: 100%;
  border: 3px solid #2b5278;
  border-radius: 10px;
  border-left: none;
  border-right: none;
}
.info-list-wrap>h2{
  margin-left: 10px;
  text-shadow: 1px  1px 4px #2b5278;
}
.checkout-info-list{
  font-size: 16px;
  font-weight: 700;
  line-height: 20px;
  width: 100%;
  list-style: none;
  padding: 0;
}
.checkout-info-list>li{
  background-color: black;
  border: 1px solid #2b5278;
  padding: 10px;
  word-wrap: break-word;
  margin-bottom: 5px;
  border-radius: 5px;
  border-left: none;
  border-right: none;
  text-shadow: 1px  1px 10px #2b5278;
}
.checkout-info-list>li:last-child{
  box-sizing: border-box;
  background-color: black;
  display: flex;
  line-height: 20px;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 20px;
  text-align: left;
  margin-bottom: 0;
}
.checkout-info-list>li:last-child>p{
  align-self: flex-start;
  margin-bottom: 10px;
}
.checkout-info-list>li:last-child>img{
  max-height: 250px;
  max-width: 250px;
  width: 40%;
  height: 40%;
}
.modal-footer{
  width: 100%;
}
.checkout-submit{
  outline: none;
  box-sizing: border-box;
  font-size: 20px;
  line-height: 27px;
  border-radius: 8px;
  padding: 10.5px 26px;
  border: 1px solid transparent;
  color: #ffffff;
  height: 50px;
  background: #2b5278;
  cursor: pointer;
  width: fit-content;
  margin-top: 10px;
}

.modal-timer{
  box-sizing: border-box;
  background-color: #2b5278;
  padding: 5px 10px;
  border-radius: 10px;
  font-size: 25px;
}


.Main{
  margin-top: 30px;
  margin-bottom: 20px;
  border-top: 5px solid #182533;
  border-bottom: 5px solid #182533;
  border-radius: 10px;
  width: 80%;
  text-shadow: 1px  1px 50px #2b5278;
}

.Footer{
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 80%;
  margin-top: auto;
  padding-bottom: 20px;
}
#inSumm{
  display: none;
}

@media (max-width: 900px){
  .Footer{
    padding-top: 20px;
    padding-bottom: 20px;
  }
  .modal{
    display: block;
    max-width: 100%;
    max-height: 100%;
    overflow: scroll;
  }
  .final-screen{
    overflow: unset;
  }
  .modal-body{
    min-width: 98%;
  }
  .modal-body>div{
    width: 100%;
  }
  .modal-inputs{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
  }
  .arrowDown{
    margin-bottom: 5px;
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
  }
  .checkout-info>p{
  font-size: 14px;
  max-width: 100%;
  color: #2b5278;
  }  
}

@media (max-width: 400px){
  .checkout-info-list>li:last-child>img{
    width: 100%;
    height: 100%;
  }
}

.support-contacts{
  border: 5px solid #182533;
  border-radius: 10px;
  padding: 4px;
  display: flex;
  justify-content: center;
  line-height: 16px;
  align-items: center;
}
.logo-support{
  display: inline-block;
  width: 30px;
  height: 30px;
  background: url(../public/Telegram_logo.png) no-repeat;
  background-size: contain;
  box-sizing: border-box;
  margin: 0px 5px 0px 0px;
}

#inCurrency{
  display: none;
}
#outCurrency{
  display: none;
}